@blue: #0078DC;
@gray: #C4C4C4;
@light-gray: #CFCFCF;
@ultra-light-gray: #EFEFEF;

html, body {
	width: 100%;
	margin: 0;
	header {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		padding: 10px 15px;
		img {
			height: 42px;
		}
		a {
			text-decoration: none;
			color: white;
			padding: 5px;
			white-space: nowrap;
		}
	}

	.main {
		z-index: 1;
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;
		.banner{
			margin-bottom: 10px;
			width: 100%;
			.main-img {
				width: 100%;
			}
		}
		.main-item {
			background-color: @gray;
			margin: 10px 15px;
			padding: 2em;
		}
		.infos{
			.info {
				padding: 0.7em;
				img {
					display: none;
				}
				.info-title {
					font-weight: bolder;
					transition: all 0.5s ease-out;
				}
				.info-desc {
					transition: all 0.5s cubic-bezier(0.6, 0.06, 0.75, 1.37);
					opacity: 0;
					height: 0;
					overflow: hidden;
				}
			}
			.info.active {
				background-color: @light-gray;
				.info-title {
					color: @blue;
				}
				.info-desc {
					opacity: 1;
					height: auto;
					margin: 1em 0;
				}
			}}
		.sales, .block3, .details{
			display: none;
		}
	}

	footer {
		background-color: @blue;
		height: 330px;
		margin-top: 10px;
	}
}

@media (max-width: 960px) {
	.menu {
		position: relative;
		.menu-burger {
			background-color: @blue;
			height: 42px;
			width: 42px;
			border: none;
		}
		.menu-items {
			display: flex;
			flex-direction: column;
			background-color: @blue;
			transition: all 1s ease-in-out;
			width: 42px;
			height: 0;
			opacity: 0;
			z-index: 999;
			overflow: hidden;
			position: absolute;
			right: 0;
			top: 42px;
			text-align: center;
		}
	}
	.menu.active {
		.menu-items {
			height: auto;
			width: auto;
			opacity: 1;
		}
	}
}

@media (min-width: 768px) {
	header {
		img {
			height: 63px;
		}
	}

	.main {
		position: relative;
		.banner {
			height: 320px;
			margin-left: 15px;
			width: calc(100vw - (180px - 4em) - 95px) !important;
			.main-img{
				height: inherit;
				width: inherit;
			}
		}
		.quotes {
			margin: 0 15px 0 0 !important;
			position: absolute;
			right: 0;
			width: calc(180px - 4em);
			height: calc(320px - 4em);
		}
		.news {
			height: 150px;
			padding: 41px 148px;
		}
		.info {
			display: flex;
			flex-direction: row;
			padding: 0;
			.info-img {
				width: 180px;
				height: 120px;
				display: block !important;
				opacity: 0;
			}
			.info-text {
				padding: 17px 27px;
				.info-desc {
					opacity: 0;
					height: auto;
					margin: 1em 0;
				}
			}
		}
		.info.active {
			background-color: @ultra-light-gray;
			.info-img {
				opacity: 1;
			}
			.info-text {
				.info-desc {
					opacity: 1;
				}
			}
		}
	}

	footer{
		margin-top: 15px;
	}
}

@media (min-width: 960px) {
	header{
		flex-direction: column !important;
		align-items: flex-start !important;
		width: initial;
		padding: 0 !important;
		img{
			margin: 20px 0 8px calc((100% - 960px) / 2 + 40px);
		}
		.menu{
			position: relative;
			width: 100%;
			.menu-burger{
				display: none;
			}
			.menu-items{
				background-color: @blue;
				padding: 1em 1em 1em calc((100% - 960px) / 2  + 35px);
			}
		}
	}
	.main {
		max-width: 960px;
		margin: 23px auto 0 auto;
		display: grid !important;
		grid-template-columns: repeat(5, 180px);
		grid-template-rows: 320px 375px;
		grid-column-gap: 15px;
		grid-row-gap: 25px;
		.main-item {
			margin: 0 !important;
		}
		.banner {
			grid-row-start: 1;
			grid-column-start: 1;
			grid-column-end: 5;
			margin: 0;
			width: 100% !important;
			position: relative;
			.main-img {
				width: 70% !important;
			}
			.details {
				padding-top: 10em;
				position: absolute;
				display: block;
				right: 0;
				top: 0;
				height: calc(100% - 12em);
				width: 30%;
				background-color: @blue;
				color: white;
				a{
					text-decoration: none;
					color: white;
				}
			}
		}
		.quotes{
			grid-row-start: 1;
			grid-column-start: 5;
			grid-column-end: 6;
		}
		.sales, .block3{
			 display: block !important;
		 }
		.news, .infos, .sales, .block3{
			grid-row-start: 2;
		}
		.news {
			grid-column-start: 1;
			grid-column-end: 3;
			height: auto !important;
		}
		.infos{
			.info1{
				padding: 0 !important;
				flex-direction: column-reverse;
				height: 100%;
				.info-text{
					padding: 0.5em;
					.info-desc {
						opacity: 0;
						height: auto;
						overflow: hidden;
					}}
			}
			.info2, .info3{
				display: none !important;
			}
			//.info.active {
			//	img {
			//		display: none;
			//	}
			//}
		}
		.sales{
			grid-row-start: 2;
			grid-column-start: 4;
			grid-column-end: 5;
		}
		.block3{
			grid-row-start: 2;
			grid-column-start: 5;
			grid-column-end: 6;
		}
	}
	footer{
		margin-top: 60px !important;
	}
}